html, body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: inherit;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  padding: 30px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#button {
  text-align: center;
}

.Rules {
  position: absolute;
  width: 80%;
  text-align: center;
  margin-left: 7em;
}

#userData {
  display: inline-block;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: 1em;
  padding: 1em;
  text-align: left;
  color: black;
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 7.5px;
}

#card {
  padding: 4em;
  text-align: center;
}

#circular-logo {
  position: fixed;
  top: 1em;
  right: 1em;
  height: 15%;
  z-index: 100;
}

.question {
    position: relative;
    padding: 2em;
    background-color: white;
    margin-top: 3em;
    margin-bottom: 1em;
    margin-left: auto;
    margin-right: auto;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    border-radius: 7.5px;
    height: 80%;
}

#quanda {
  padding: 30px;
  right : 100;
  position: relative;
  right: 100px; 
  width: -webkit-max-content; 
  width: -moz-max-content; 
  width: max-content;
  background-color: #61dafb;
  z-index: 100;
}

.MuiFormControl-root-1 {
  
}

.answer {
  top: 400px;
  margin: auto;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  border-radius: 7.5px;
  margin: auto;
  margin-top: 1em;
  text-align: center;
}

#submit {
  padding: 30px;
}

.center-div
{
  margin: 0 auto;
  padding: 20px;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

